.NavBar
{
    position       : fixed;
    top            : 0;
    left           : 0;
    bottom         : 0;
    display        : flex;
    flex-direction : column;
    align-content  : center;
    justify-content: center;
}

@media screen and (orientation:portrait), (max-width:800px)
{
    .NavBar
    {
        background-color: #1E2226;
        position        : fixed;
        bottom          : 0;
        left            : 0;
        right           : 0;
        top             : auto;
        display         : flex;
        flex-direction  : row;
        align-content   : center;
        justify-content : center;
    }
}
