.Project-Brief
{
    font-family   : ubuntu, sans-serif;
    font-weight   : 300;
    font-style    : normal;
    font-size     : 24px;
    color         : #C0C0C0;
    display       : flex;
    flex-direction: row;
}

.Project-Left
{
    text-align: left;
}

.Project-Right
{
    text-align: right;
}

.Project-Title
{
    font-family   : ubuntu, sans-serif;
    font-weight   : 700;
    font-style    : normal;
    font-size     : 48px;
    color         : #C9C9C9;
    margin        : 0;
    text-transform: uppercase;
}

.Project-Block
{
    flex   : 1;
    padding: 15px;
}

.Project-Description
{
    border-top   : solid #C0C0C0;
    border-bottom: solid #C0C0C0;
    padding      : 10px 0px;
    margin       : 10px 0px;
}

/* -----------------------------------------------------------------------------
Links
----------------------------------------------------------------------------- */

.Project-Link
{
    font-family   : ubuntu, sans-serif;
    font-weight   : 700;
    font-style    : normal;
    font-size     : 24px;
    display       : flex;
    flex-direction: row;
    align-content : center;
}

.Project-Link span
{
    display       : flex;
    flex-direction: row;
    margin        : 0px 10px;
}

.Project-Link a
{
    font-family    : ubuntu, sans-serif;
    font-weight    : 400;
    font-style     : italic;
    text-decoration: none;
    color          : #C0C0C0;
    height         : 45px;
    margin         : 0px 10px;
}

/* -----------------------------------------------------------------------------
Hashtags
----------------------------------------------------------------------------- */

.Project-Hashtag
{
    display       : flex;
    flex-direction: row;
    padding       : 10px 0px;
}

.Project-Hashtag span
{
    border-radius: 5px;
    color        : #393A3D;
    font-family  : ubuntu, sans-serif;
    font-weight  : 400;
    font-style   : normal;
    font-size    : 24px;
    padding      : 5px 10px;
    margin       : 0px 10px 0px 0px;
}

.Project-Hashtag .programming
{
    background-color: #2E8B57;
}

.Project-Hashtag .leadership
{
    background-color: #DAA520;
}

.Project-Hashtag .design
{
    background-color: #87CEEB;
}

@media screen and (max-width:500px)
{
    .Project-Link span
    {
        flex-direction: column;
    }

    .Project-Link a
    {
        margin-bottom: 10px;
        padding      : 10px 10px;
        border       : solid #FFFFFF;
    }
}
