.NavTab
{
    background-color: #1E2226;
    width           : fit-content;
    display         : flex;
    flex-direction  : row;
    align-items     : center;
    justify-content : left;
    padding         : 0px 15px;
    border-right    : solid #556573;
}

.NavTab:hover
{
    border-top   : solid #556573;
    border-bottom: solid #556573;
}

.NavTab .Name
{
    color         : #8195A6;
    overflow      : hidden;
    max-width     : 0px;
    transition    : max-width 0.2s ease-out;
    padding       : 10px 0px 10px 0px;
    white-space   : nowrap;
    text-transform: lowercase;
    font-family   : ubuntu-mono,monospace;
    font-weight   : 400;
    font-style    : normal;
    font-size     : 20px;
}

.NavTab img
{
    height : 50px;
    padding: 10px;
}

@media screen and (orientation:portrait), (max-width:800px)
{
    .NavTab
    {
        padding         : 10px 0px;
        border-right    : none;
    }

    .NavTab:hover
    {
        border-top   : none;
        border-bottom: none;
    }
}
