.Footer
{
    background-color: #1E2226;
    width           : 100%;
    border-top      : solid #556573;
}

.Footer .Credit
{
    text-align : center;
    padding    : 30px;
    font-size  : 24px;
    color      : #556573;
    font-family: ubuntu-mono,monospace;
    font-weight: 400;
    font-style : normal;
    margin     : 0;
}

.Credit a
{
    font-weight    : bold;
    text-decoration: underline;
    color          : inherit;
}

.Footer .Buffer
{
    padding   : 0px;
    height    : 0px;
    width     : 100%;
    transition: height 0.2s ease-out;
}

@media screen and (orientation:portrait), (max-width:800px)
{
    .Footer .Buffer
    {
        height: 100px;
    }
}
