@keyframes Cursor-Blink
{
    0%
    {
        opacity: 0.0;
    }
    50%
    {
        opacity: 0.5;
    }
    100%
    {
        opacity: 0.0;
    }
}

@media (prefers-reduced-motion: no-preference)
{
    .Blinking
    {
        animation: Cursor-Blink infinite 1s step-start;
    }
}
