.Header-Container
{
    background-color: #1E2226;
}

.Header-Logo
{
    min-height    : 120px;
    height        : 40%;
    pointer-events: none;
}

.Header
{
    overflow        : hidden;
    background-color: inherit;
    min-height      : 150px;
    height          : 100vh;
    width           : calc(100vw - 210px);
    margin          : auto;
    display         : flex;
    flex-direction  : row;
    align-items     : center;
    justify-content : center;
    color           : #FFFFFF;
}

.Header-Text
{
    display       : flex;
    flex-direction: column;
    padding       : 0 20px;
}

.Header-Title
{
    color         : #8195A6;
    font-family   : ubuntu, sans-serif;
    font-weight   : 700;
    font-style    : normal;
    font-size     : 64px;
    text-transform: uppercase;
    margin        : 0;
}

.Header-Subtitle
{
    color         : #556573;
    font-family   : ubuntu-mono,monospace;
    font-weight   : 400;
    font-style    : normal;
    font-size     : 36px;
    text-transform: lowercase;
    margin        : 0;
}

@media screen and (orientation:portrait), (max-width:800px)
{
    .Header
    {
        width         : 100%;
        flex-direction: column;
    }

    .Header-Text
    {
        text-align: center;
    }
}
