.AspectRatio
{
    position: relative;
    width   : 100%;
}

.AspectRatio._16-9
{
    padding-top: 56.25%;
}

.AspectRatio._4-3
{
    padding-top: 75%;
}

.AspectRatio._3-2
{
    padding-top: 66.66%;
}

.AspectRatio._8-5
{
    padding-top: 62.5%;
}

.AspectRatio *:first-child
{
    position: absolute;
    top     : 0;
    left    : 0;
    bottom  : 0;
    right   : 0;
}
