.Contacts
{
    position        : fixed;
    top             : 0;
    right           : 0;
    width           : fit-content;
    background-color: #1E2226;
    padding         : 20px 10px 20px 20px;
    border-radius   :  0px  0px  0px 10px;
    overflow        : hidden;
    display         : flex;
}

.Contact
{
    display        : flex;
    flex-direction : row;
    align-items    : center;
    justify-content: left;
}

.Contacts .Label
{
    font-family    : ubuntu, sans-serif;
    font-weight    : 300;
    font-style     : normal;
    font-size      : 20px;
    text-decoration: none;
    overflow       : hidden;
    white-space    : nowrap;
    padding-right  : 0px;
    max-width      : 0px;
    transition     : max-width 0.2s ease-out;
}

.Contacts img
{
    width        : auto;
    height       : 36px;
    padding-right: 10px;
}
