.Profile
{
    display            : flex;
    flex-direction     : row;

    height             : fit-content;

    background-image   : url("../media/profile_bg.png");
    background-position: center;
    background-repeat  : no-repeat;
    background-size    : cover;
}

.Profile-Block
{
    font-family   : ubuntu, sans-serif;
    font-weight   : 300;
    font-style    : normal;
    font-size     : 24px;
    color         : #C0C0C0;

    display       : flex;
    flex-direction: column;

    flex          : 1;
    padding       : 0px 50px;
    margin        : auto;
}

.Profile-Margin
{
    width: 100px;
}

.Profile-Padding
{
    flex: 1;
}

.Profile-Content
{
    flex      : 1;
    text-align: center;
    padding   : 25px 0px 50px 0px;
}

.Profile-Img
{
    padding: 50px 0px 25px 0px;
    width  : 256px;
    margin : auto;
}

.Icon
{
    height       : 50px;
    padding-right: 15px;
}

@media screen and (max-width:1280px), screen and (orientation:portrait)
{
    .Profile
    {
        background-image: url("../media/bg.png");
    }

    .Profile-Padding
    {
        flex: 0;
    }
}
